/* ANIMATION */
._shapeAnimation {
  position: relative;
}

._shapeAnimation::after {
  content: "";
  height: 100vh;
  width: 100vh;
  background-color: rgba(0, 195, 255, 0.15);
  position: absolute;
  top: 10%;
  right: -40%;
  z-index: -1;
  border-radius: 50%;
}

._shapeAnimation::before {
  content: "";
  height: 100vh;
  width: 100vh;
  background-color: rgba(0, 195, 255, 0.15);
  position: absolute;
  top: 62%;
  left: -40%;
  z-index: -1;
  border-radius: 50%;
}
/* ANIMATION END */

