/* Footer_style */
.footer::before {
  content: "";
  width: 50px;
  height: 50px;
  margin-top: 50px;
  border-radius: 50%;
  background-color: red;
  opacity: 0.1;
  position: absolute;
  -webkit-animation: _move 3s linear infinite alternate;
          animation: _move 3s linear infinite alternate;
}

@-webkit-keyframes _move {
  from {
    top: 0px;
  }

  to {
    top: 40px;
  }
}

@keyframes _move {
  from {
    top: 0px;
  }

  to {
    top: 40px;
  }
}

.footer::after {
  content: "";
  position: absolute;
  height: 200px;
  width: 200px;
  background-color: #d6e4f0;
  border-radius: 50%;
  opacity: 0.3;
  z-index: -1;
  -webkit-animation: _moveBigCircle 3s infinite alternate;
          animation: _moveBigCircle 3s infinite alternate;
}

@-webkit-keyframes _moveBigCircle {
