.contact_container::before{
    content: "";
    width: 0;
    height: 0;
    left: 50px;
    border-top: 90px solid transparent;
    border-right: 150px solid rgba(0, 255, 179,0.19);
    border-bottom: 90px solid transparent;
    position: absolute;
    -webkit-animation: rotate 20s linear infinite alternate;
            animation: rotate 20s linear infinite alternate;
}
@-webkit-keyframes rotate{
   from{
       -webkit-transform: rotate(0deg);
               transform: rotate(0deg);
   }to{
       -webkit-transform: rotate(360deg);
               transform: rotate(360deg);
   }
}
@keyframes rotate{
   from{
       -webkit-transform: rotate(0deg);
               transform: rotate(0deg);
   }to{
       -webkit-transform: rotate(360deg);
               transform: rotate(360deg);
   }
}


.contact_container::after{
    content: "";
    height: 30px;
    width: 30px;
    border: 2px solid gray;
    opacity: 0.5;
    border-radius: 50%;
    top: 5%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute;
    -webkit-animation: rotate_circle 12s linear infinite alternate;
            animation: rotate_circle 12s linear infinite alternate;
}

@-webkit-keyframes rotate_circle{
    0%{
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    25%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateX(-60px);
                transform: translateX(-60px);
    }
    75%{
        -webkit-transform: translateY(60px);
                transform: translateY(60px);
    }
    100%{
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
}

@keyframes rotate_circle{
    0%{
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }
    25%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateX(-60px);
                transform: translateX(-60px);
    }
    75%{
        -webkit-transform: translateY(60px);
                transform: translateY(60px);
    }
    100%{
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
}

